@import "../../abstracts/variables.scss";

.cash {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 10px;
  border: 2px solid $color-dark-purple;
  background: linear-gradient(#fdf, #fff);

  &__title {
    padding: 20px;
  }

  &__box {
    display: flex;
    gap: 20px;
    width: 100%;
    padding: 20px;
    flex-wrap: wrap;
    //  border: 2px solid $color-dark-purple;
  }
  &__sum {
    padding: 0px 0px 20px 0px;
  }
  &__span {
    font-weight: 600;
  }
}
